/* eslint-disable jsx-a11y/anchor-is-valid */
import {
  FileDoneOutlined,
  HomeOutlined,
  MenuOutlined,
  OrderedListOutlined,
  RightOutlined,
} from "@ant-design/icons";
import {Menu, Tag} from "@pankod/refine-antd";
import {Layout} from "antd";
import {
  Space,
  Icons,
  Avatar,
  Typography,
  Grid,
  Drawer,
  Spin,
} from "@pankod/refine-antd";
import {useGetIdentity, useLogout, useNavigation} from "@pankod/refine-core";
import {useEffect, useState} from "react";
import configs from "configs";
import {menuItemColor, menuItemDiv} from "components/layout/sider/styles";
import {Link} from "react-router-dom";
import quizzer from "./app_config";
import {SystemHeaderMenu} from "apps/core/components/system_header_menu";
import {SchoolData, SchoolOperatingTypeData} from "apps/core/interfaces";
import {gqlDataProvider} from "api";
import {Header} from "components/layout";
import { SystemLogoComponent } from "apps/core/components/common/system_logo_component";

const {UserOutlined} = Icons;
const {Text} = Typography;

export const QuizzerHeader: React.FC = () => {
  const {data: user} = useGetIdentity();
  const [visible, setVisible] = useState(false);
  const {mutate: logout} = useLogout();
  const {push} = useNavigation();
  const breakpoint = Grid.useBreakpoint();

  const isMobile = !breakpoint.lg;

  const [collapsed, setCollapsed] = useState<boolean>(false);
  const [loadingOperationSchool, setLoadingOperationalSchool] = useState(false);
  const [operationalSchool, setOperationalSchool] = useState<SchoolData>();
  const [operatingTypes, setOperatingTypes] = useState<
    SchoolOperatingTypeData[]
  >([]);
  const [operatingTypesCodes, setOperatingTypesCodes] = useState<String[]>([]);

  const getOperationSchool = async () => {
    setLoadingOperationalSchool(true);
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "operationalSchool",
        fields: [
          "school",
          {
            operatingTypes: [
              "id",
              {
                schoolType: ["id", "name", "code"],
              },
            ],
          },
          {
            data: ["id", "serialNumber", "name"],
          },
        ],
      },
    });
    setOperatingTypesCodes(
      data.operatingTypes.map((item: SchoolOperatingTypeData) => {
        return item.schoolType.code;
      })
    );
    setOperatingTypes(data.operatingTypes);
    setOperationalSchool(data.data);
    setLoadingOperationalSchool(false);
  };

  useEffect(() => {
    getOperationSchool();
    setCollapsed(isMobile);
  }, [isMobile]);

  const appLogoTitle = (
    <div style={{display: "flex", margin: "8px 4px", padding: "4px"}}>
      <Space align="center">
        <span style={{marginRight: 10}}>
          <a onClick={() => setVisible(!visible)} style={{color: "gray"}}>
            <MenuOutlined style={{fontSize: 24, marginTop: 24}} />
          </a>
        </span>
        <SystemLogoComponent app={quizzer.app_name} />
      </Space>
    </div>
  );

  const appMenu = (
    <Drawer
      title={
        <div style={{display: "flex", margin: "8px 4px", padding: "4px"}}>
          <Space align="center">
            <span style={{marginRight: 10}}>
              <Text
                ellipsis
                strong
                style={{
                  fontSize: 24,
                  paddingLeft: 5,
                  marginBottom: 5,
                  marginLeft: 10,
                }}
              >
                <Link to={"/"} style={{textDecoration: "none", color: "black"}}>
                  {configs.system_name}
                </Link>{" "}
                <Tag color={"green"} style={{display: "block", marginTop: 10}}>
                  {quizzer.app_name}
                </Tag>
              </Text>
            </span>
          </Space>
        </div>
      }
      placement="left"
      size={"default"}
      width={330}
      closable={true}
      onClose={() => setVisible(!visible)}
      visible={visible}
    >
      <Menu selectedKeys={[]} mode="inline">
        <Spin spinning={loadingOperationSchool}>
          <Menu.Item
            style={menuItemColor}
            key={"files"}
            icon={<HomeOutlined style={{color: "orange"}} />}
            onClick={() => push("/quizzer")}
          >
            <div style={menuItemDiv}>
              {"Home"}
              {collapsed && <FileDoneOutlined />}
            </div>
          </Menu.Item>
        </Spin>
      </Menu>
    </Drawer>
  );

  return <Header app={appLogoTitle} appMenu={appMenu} />;
};
